import {
  StyledOrder,
  OrderInput,
  OrderValue,
} from './styles';
import { Title, TitleLevel } from '../title/Title';
import { Description } from '../description/Description';
import { Button } from '../button/Button';
import { useState } from 'react';

export const Order = ({ order }) => {
  const [address, setAddress] = useState('');

  const handleBuyClick = () => {
    // eslint-disable-next-line no-alert
    alert(`  ,  :\n${order.map(
      (product) =>
        `${product.name} - ${product.price} .\n`
    )}
    : ${fullPrice} .
      : ${address}.`);
  };

  const fullPrice = order.reduce(
    (sum, product) => (sum += product.price),
    0
  );

  return (
    <StyledOrder>
      <Title level={TitleLevel.H4} mB={24}>
         
      </Title>
      <OrderInput
        value={address}
        onChange={(e) => setAddress(e.target.value)}
        placeholder="  "
        name="address"
        type="text"
      />
      <Description fS={14} lH={21}>
        
      </Description>
      <OrderValue>{fullPrice} .</OrderValue>
      <Button
        onClick={handleBuyClick}
        disabled={!(order.length && address)}
      >
        
      </Button>
    </StyledOrder>
  );
};
